		personalNote: `He is of two spirits. Yin and yang could have been his first and last names. He switches his hips when he walks and when he laughs it rolls from his belly like a deep wave. He sometimes feels she and she sometimes feels he. They remember their mothers‚Äô words about their gender ambiguity as a youth. ‚ÄúNo te vistas as√≠ que pareces una marimacha!‚Äù. ‚ÄúSaca el pecho, camina como una mujer!‚Äù Estas palabras fuertes y dolorosas were slapped onto their skin since puberty struck them and the flourishing of their breasts demanded that they no longer play with cars and dirt. A strictly gendered child, the most binary of binaries is what their Mami demanded of them. They still wear the scars from these words used to described women like them, ‚Äúmarimacha‚Äù, ‚Äútortillera‚Äù. These scars now guide them to embrace their he & she, sin miedo. They now understand know that the heart of a two-spirit person es un gran tesoro. Un Tesoro que merece proteccion y ternura. Ellxs saben ahora what everyone knew back then, that their mother gave birth to two children, two humans en el mismisimo cuerpo. And now, they are learning to how to love them both.`
