		audioUrl: sofia_en_techo_audio,
		personalNote: `<br/>I remember for a moment I wanted to move to NY. That‚Äôs the moment Ximena took that photo, it‚Äôs been a year to be exact, and so many things have changed our lives, many lives have changed forever. I could not imagine the things that were gonna happen, all I knew is that i needed to return back home and be back in my bed. I needed to be back with my friends in Mexico City, that‚Äôs where I live now. <br/><br/>

    Yo creo que la vida cambio demasiado, que te dir√≠a a ti socia‚Ä¶lo primero que te dir√≠a es que no dejes de hacer el trabajo que estas haciendo, no dejes de tejer alianzas, las cuales me est√°n ayudando a mi a crecer como persona y como mujer pero tambi√©n estoy aprendiendo a conocer otras vidas y a otras hermanas a trav√©s del trabajo que se esta haciendo. Nunca pens√© que el trabajo que estoy haciendo fuera a ser reconocido, pero ya en este momento, ya ni me interesa. Me interesa mas ser reconocida por mi comunidad, mis hermanas, y aquellos los cuales he escogido que sean parte de mi vida como tu Ximena. Gracias gracias por darme alojo las veces que he llegado a Nueva York, gracias por darme alojo en tu casa, gracias por las platicas que hemos tenido en tu cocina. <br/><br/>
    
    Lo √∫nico que te puedo decir Sofia es que la vida va a cambiar mucho, y una tiene que ser fuerte.  <br/><br/>
    
    I want to recognize the efforts of so many people now, and then to make a better trans future, not only for ourselves but for others. Even under these circumstances, our community, my trans community will make space and time to thrive because we deserve a better future for us, as elder women for our younger generation, seeking hope in such a time of uncertainty or I just cannot imagine living a life without any physical contact as a young adult.<br/><br/>
    
    I want to tell myself that things are going to be better, and that I shouldn‚Äôt give up and I should continue doing the work that I do. I‚Äôm playing a little bit of rancheras cause that‚Äôs how I feel, it‚Äôs a day after a new moon. <br/><br/>
    
    I wanna encourage more people to remind yourselves that life has some strange surprises and that we should just continue to live our lives and those with privilege to give us a little bit of access and recognize that we exist and that we are trying hard to be afloat and survive in such a chaotic time. <br/><br/>
    
    Gracias Ximena, for taking this photo and reminding me that in a year so many things can change, and I‚Äôm actually beyond excited for the future. For my future. For the trans future. For our queer future that we are building together, thank you, I hope to see you soon and hug you again.`
