		personalNote: `Halloween 2019. I dressed as Rue from Euphoria. In the first season of the show, they never explicitly name Rue‚Äôs gender or sexuality. Queer characters are allowed complexity in that their queerness is not the driving force behind their character arc, though it is acknowledged and certainly part of the plot. Rue falls in love with Jules and they‚Äôre queer but it‚Äôs not a ‚Äúcoming out story.‚Äù I think Rue‚Äôs gender is referenced once when Jules dresses them and says, ‚ÄúI like the way I dressed you but I‚Äôm worried I fucked with your gender expression.‚Äù That moment reminded me of the intentional decision I made to look for looser-fitting clothing years ago, before I felt comfortable asking anyone to use they/them pronouns when referring to me. I see my own gender and sexuality reflected in Rue. I see myself before I had the words ‚Äúqueer‚Äù or ‚Äúnon-binary‚Äù or ‚Äúfluid‚Äù as descriptors and I like to imagine what it‚Äôd be like not to feel the need to define or disclose those identities; to live in a world where queerness doesn‚Äôt require explanation. `
